import React from 'react';
import { Route, Routes } from 'react-router-dom';
import { StakingContextProvider } from './components/context/staking';
import Layout from './components/templates/master';

const NFTStaking = React.lazy(() => import('./components/pages/nft-staking'));
const Home = React.lazy(() => import('./components/pages/home'));
const Land = React.lazy(() => import('./components/pages/land'));
const Dome = React.lazy(() => import('./components/pages/dome'));

function App() {
  return (
    <React.Suspense>
      <Layout>
        <Routes>
          {
            //<Route path="/" element={<Home />} />
          }
          <Route path="/domes" element={<Dome />} />
          <Route path="/land" element={<Land />} />
          <Route path="/" element={<StakingContextProvider><NFTStaking /></StakingContextProvider>} />
        </Routes>
      </Layout>
    </React.Suspense>
  );
}

export default App;
