import classNames from 'classnames';
import { NavLink, NavLinkProps } from 'react-router-dom';
import React from 'react';

export default function Link({ className, children, ...props }: NavLinkProps) {
  return (
    <NavLink
      className={classNames('p-4 text-zinc-400 hover:text-zinc-100 transition', className)}
      {...props}
    >
      {children}
    </NavLink>
  );
}
