import React, { useState, useContext, useMemo, useEffect } from 'react';

type StakingContextProps = {
  stakingFromEpoch: number,
  setStakingFromEpoch: React.Dispatch<any>,
  showOnlyOwned: boolean,
  setShowOnlyOwned: React.Dispatch<any>,
};

const StakingContext = React.createContext<StakingContextProps>({
  stakingFromEpoch: 358,
  showOnlyOwned: false,
  setStakingFromEpoch: () => { return },
  setShowOnlyOwned: () => { return }
});

function StakingContextProvider({ children }: { children: React.ReactNode }) {
  const [stakingFromEpoch, setStakingFromEpoch] = useState<number>(358);
  const [showOnlyOwned, setShowOnlyOwned] = useState<boolean>(true);

  const context = useMemo(() => ({
    stakingFromEpoch, setStakingFromEpoch, showOnlyOwned, setShowOnlyOwned
  }), [stakingFromEpoch, showOnlyOwned]);

  return (
    <StakingContext.Provider value={context}>
      {children}
    </StakingContext.Provider>
  );
}

export { StakingContextProvider, StakingContext };
